<template>
  <div>
    <!-- ref="refTextEditor" -->
    <TextEditor
      v-model="data"
      :options="ckeditor"
      with-image-upload>
      <template #toolbar>
        <Button
          class="ml-2"
          color="teal"
          dense
          :loading="loading"
          @click="handlePreviewEmailTemplate">
          <i class="material-icons mr-1">visibility</i>
          Preview Email
        </Button>
        <Button
          class="ml-auto"
          color="orange"
          view="smooth"
          dense
          @click="openModalReset">
          <i class="material-icons">restart_alt</i>
          Reset to Default
        </Button>
      </template>
    </TextEditor>
    <small
      v-for="(error, errorIndex) of validation?.body?.$errors"
      :key="errorIndex"
      class="mt-2 text-red-base block">
      {{ error.$message }}.
    </small>

    <!-- v-model="true" -->
    <Modal
      v-model="modal"
      hide-footer
      hide-header
      size="xs">
      <template #default>
        <div class="py-10">
          <img
            class="mb-10 mx-auto"
            src="@/assets/icon/warning.svg"
            alt="Warning icon"
          />
          <div class="mt-8 mb-10">
            <h5 class="mb-4 text-center">Reset Email Template</h5>
            <p class="text-center mb-6">
              Are you sure you want to reset to the default email template?
            </p>
          </div>
          <div class="text-center">
            <Button
              view="outlined"
              color="teal"
              class="bg-white"
              @click="modal = false"
              :disabled="loading">
              Cancel
            </Button>
            <Button
              view="smooth"
              color="orange"
              class="ml-3"
              :loading="loading"
              @click="handleResetTemplate">
              Yes, reset
            </Button>
          </div>
        </div>
      </template>
    </Modal>

    <PreviewEmailTemplate
      v-model:show="showPreview"
      :body="preview"
      render-type="direct"
    />
  </div>
</template>
<script>
import { defineComponent, reactive, computed, ref } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { useUser } from 'composable/user'
import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build'

import PreviewEmailTemplate from 'components/EmailTemplate/PreviewEmailTemplate.vue'
import TextEditor from 'components/TextEditor.vue'

import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'

export default defineComponent({
  name: 'GeneralNotificationsFormEditor',
  components: {
    PreviewEmailTemplate,
    TextEditor,
    Button,
    Modal
  },
  props: {
    modelValue: {
      type: String,
      default: undefined
    },
    validation: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:modelValue', 'reset'],
  setup(props, ctx) {
    const store = useStore()
    const route = useRoute()
    const { me } = useUser()
    const ckeditor = reactive({
      editor: ClassicEditor,
      config: {
        htmlSupport: {
          allow: [
            {
              styles: true,
              classes: true
            }
          ]
        },
        link: {
          decorators: {
            test: {
              mode: 'automatic',
              callback: (url) => url === '{:verify-link}',
              classes: 'btn btn-blue btn-filled my-4'
            }
          }
        },
        simpleUpload: {
          uploadUrl: `${import.meta.env.VITE_BASE_URL_API}/v1/pos/settings/email-template/upload-image/${route.params.id}?buyback_company_token=${me.value.buyback_company_token}`
        }
      }
    })
    const data = computed({
      get: () => props.modelValue,
      set: (value) => ctx.emit('update:modelValue', value)
    })
    const preview = ref()
    const showPreview = ref(false)
    const loading = ref(false)
    const modal = ref(false)
    const handlePreviewEmailTemplate = async() => {
      loading.value = true
      try {
        const response = await store.dispatch('setting/emailTemplate/previewEmailTemplate', {
          id: route.params.id, body: data.value || ''
        })
        preview.value = response
        showPreview.value = true  
      } catch (error) {        
        preview.value = null
        showPreview.value = false
      } finally {
        loading.value = false
      }
    }
    const openModalReset = () => {
      modal.value = true
    }
    const handleResetTemplate = async() => {
      try {
        await store.dispatch('setting/emailTemplate/resetEmailTemplate', route.params.id)
        modal.value = false
        ctx.emit('reset')
      } catch (error) {
      } finally {
        loading.value = false
      }
    }

    return {
      ckeditor,
      data,
      preview,
      showPreview,
      loading,
      modal,
      openModalReset,
      handleResetTemplate,
      handlePreviewEmailTemplate
    }
  }
})
</script>