<template>
  <div v-loading="loading" class="space-y-10">
    <GeneralNotificationsFormInfo
      v-model="data"
      :validation="validation"
      :hide-bcc="isHideBcc"
      ref="formInfo"
    />
    <GeneralNotificationsFormEditor
      v-model="data.body"
      :validation="validation"
      ref="formEditor"
      @reset="resetTemplate"
    />
    <GeneralNotificationsFormCodes
      v-model="data.sort_code"
      :loading="loading"
    />
    <!-- <div class="flex justify-end -mx-6 px-6 border-t pt-4 border-grey-01">
      <Button
        color="teal"
        :loading="loading"
        @click="saveTemplate">
        Save
      </Button>
    </div> -->
  </div>
</template>

<script>
import { ref, computed, defineComponent } from 'vue'
import { useRoute } from 'vue-router'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
import { useGeneralNotificationForm } from 'composable/setting/emailTemplate'

import Button from 'bbUi/components/button/button.vue'

import GeneralNotificationsFormInfo from 'components/EmailTemplate/GeneralNotifications/Form/Info/index.vue'
import GeneralNotificationsFormEditor from 'components/EmailTemplate/GeneralNotifications/Form/Editor/index.vue'
import GeneralNotificationsFormCodes from 'components/EmailTemplate/GeneralNotifications/Form/Codes/index.vue'

export default defineComponent({
  name: 'GeneralNotificationsForm',
  components: {
    Button,
    GeneralNotificationsFormInfo,
    GeneralNotificationsFormCodes,
    GeneralNotificationsFormEditor
  },
  async setup() {
    const route = useRoute()
    const formInfo = ref()
    const formEditor = ref()
    const {
      data,
      loading,
      handleShow,
      handleUpdate
    } = useGeneralNotificationForm()
    const isHideBcc = computed(() => {
      const excludedBccByTemplateId = ['31']
      return excludedBccByTemplateId.includes(`${data.value.id}`)
    })
    const rules = {
      subject: { required },
      body: { required }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })

    const saveTemplate = async () => {
      if (!await validation.value.$validate()) return

      const payload = {
        id: route.params.id,
        data: data.value
      }

      await handleUpdate(payload)
    }
    const resetTemplate = async () => {
      handleShow(route.params.id)
    }

    await handleShow(route.params.id)

    return {
      data,
      loading,
      isHideBcc,
      formInfo,
      formEditor,
      validation,
      handleShow,
      handleUpdate,
      saveTemplate,
      resetTemplate
    }
  }
})
</script>
