<template>
  <Breadcrumb
    class="mt-2 mb-8"
    pageTitle="Edit Notification"
    :backTo="{
      name: 'settingEmailTemplate',
    }"
    :items="breadcrumb">
    <template #right>
      <Button
        :loading="loading"
        color="teal"
        @click="handleSave">
        Save Changes
      </Button>
    </template>
  </Breadcrumb>
  <Card>
    <GeneralNotificationsForm ref="generalForm" />
  </Card>
</template>

<script>
import { defineComponent, ref } from 'vue'

import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import GeneralNotificationsForm from 'components/EmailTemplate/GeneralNotifications/Form/index.vue'

export default defineComponent({
  components: {
    Card,
    Button,
    Breadcrumb,
    GeneralNotificationsForm
  },
  async setup() {
    const breadcrumb = [
      {
        text: 'Settings',
        to: { name: 'setting' }
      },
      {
        text: 'Notifications',
        to: { name: 'settingEmailTemplate'}
      },
      {
        text: 'Edit Notification'
      }
    ]
    const generalForm = ref(null)
    const loading = ref(false)
    const handleSave = async () => {
      await generalForm?.value?.saveTemplate()
    }

    return {
      generalForm,
      breadcrumb,
      handleSave,
      loading
    }
  }
})
</script>

