<template>
  <div class="space-y-4">
    <div class="space-y-1 lg:w-1/2">
      <label>Title</label>
      <Input
        v-model="data.name"
        placeholder="Title"
        block
        disabled
      />
    </div>
    <div class="space-y-1 lg:w-1/2">
      <label>Email</label>
      <Input
        v-model="data.email_sender"
        placeholder="Email Sender"
        block
        disabled
      />
    </div>
    <div v-if="!hideBcc" class="space-y-1 lg:w-1/2">
      <label>BCC</label>
      <MultipleEmailInput
        @email-change="handleUpdateBccEmail"
        :suggestion="false"
        :emails="data.bcc"
      />
    </div>
    <div class="space-y-1 lg:w-1/2">
      <label>Subject</label>
      <Input
        v-model="data.subject"
        placeholder="Subject"
        block
      />
      <small
        v-for="(error, errorIndex) of validation?.subject?.$errors"
        :key="errorIndex"
        class="mt-2 text-red-base block">
        {{ error.$message }}.
      </small>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent } from 'vue'

import Input from 'bbUi/components/input/input.vue'
import MultipleEmailInput from 'components/MultipleEmailInput.vue'

export default defineComponent({
  name: 'GeneralNotificationsFormInfo',
  components: {
    Input,
    MultipleEmailInput
  },
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    },
    validation: {
      type: Object,
      default: () => ({})
    },
    hideBcc: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup(props, ctx) {
    const data = computed({
      get: () => props.modelValue,
      set: (value) => ctx.emit('update:modelValue', value)
    })
    const handleUpdateBccEmail = (emails) => {
      data.value.bcc = [...emails]
    }
    return {
      data,
      handleUpdateBccEmail
    }
  }
})
</script>
