<template>
  <Table
    v-loading="loading"
    :data="modelValue">
    <TableColumn prop="code" label="Code">
      <template #default="scope">
        <div class="flex items-center gap-x-4">
          <span>{{ scope.row.code }}</span>
          <i
            class="material-symbols-outlined text-lg text-teal-04"
            @click="copyText(scope.row.code)">
            content_copy
          </i>
          <i class="material-icons">copy</i>
        </div>
      </template>
    </TableColumn>
    <TableColumn prop="description" label="Description" />
  </Table>
</template>
<script>
import { defineComponent, computed } from 'vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'GeneralNotificationsFormCodes',
  components: {},
  props: {
    modelValue: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const data = computed(() => props.modelValue)
    const loading = computed(() => props.loading)
    const copyText = (text) => {
      navigator.clipboard.writeText(text).then(() => {
        ElMessage({
          type: 'success',
          message: 'Text copied to clipboard'
        })
      })
    }

    return {
      data,
      loading,
      copyText
    }
  }
})
</script>